import React from 'react';
import './Projects.css';

import project1 from './hotel-advisor.png';
import project2 from './chatbot.png';
import project3 from './data-pipeline.png';
import project4 from './expense-tracker.png';

class Projects extends React.Component {

  render() {
    return (
      <div id="projects" className="box">

        <div className="row" style={{ justifyContent: "center" }}>
          <h1 style={{color: "black"}}>ACADEMIC&nbsp;</h1>
          <h1 style={{color: "#3399ff"}}>PROJECTS</h1>
        </div>

        <div className="project-row">

          <div className="project-card">
            <img
              src={project1}
            />

            <div className="text-block">
              <h3>Hotel Advisor Web Application</h3>
              <h5>Technologies: Java, Jetty, MySQL, Bootstrap, MySQL, JSON, Google Maps API</h5>
            </div>

            <div className="hide">
              <ul>
                <li>Created RESTful APIs using Eclipse Jetty Servlet Engine to display hotels in the Bay area from JSON files extracted from Expedia. The hotel data was loaded into a local MySQL server using MultiThreading in Java.</li>
                <li>The Register servlet hashed and salted the password for security and the Login servlet used the same salt to verify users. The website used cookies to maintain a session.</li>
                <li>The website displayed a Google Map centered on the hotel and fetched tourist attractions in a certain desired radius in real-time by querying the Google Maps API.</li>
                <li>The website also lets users view reviews by other users and add/modify their own. The users could also save/remove hotels for later use.</li>
              </ul>
            </div>

          </div>

          <div className="project-card">
            <img
              src={project2}
            />
            <div class="text-block">
              <h3>Movie Dialogues Based Chabot</h3>
              <h5>Technologies: Python, SQLite, Tensorflow, Flask, Bootstrap</h5>
            </div>
            <div className="hide">
              <ul>
                <li>Developed an AI-based chatbot using the TensorFlow NMT(Neural Machine Translation) model in python.</li>
                <li>Downloaded and organized one month Reddit data according to the score variable and in the parent-child format in an SQLite server using SQL Transactions as the data being processed was a lot.</li>
                <li>As one month Reddit did not have a general context, the Chatbot gave vague responses, hence I trained the Chatbot again on Cornell Movie Dialogues dataset to achieve a Chatbot with a consistent context.</li>
                <li>I also created a website to interact with the Chatbot using Flask, and Bootstrap and Jinja templating for the web pages.</li>
              </ul>
            </div>
          </div>

          <div className="project-card">
            <img
              src={project3}
            />
            <div class="text-block">
              <h3>Bid Logs Processing Data Pipeline</h3>
              <h5>Technologies: Java, MapReduce, Apache Beam, Google Cloud Dataflow, Protocol Buffers, Google Cloud Storage, BigQuery</h5>
            </div>
            <div className="hide">
              <ul>
                <li>Executed an end-to-end data processing pipeline using Apache Beam in Java on Google Cloud Dataflow, which processed data utilizing Map, Shuffle, and Reduce steps in parallel.</li>
                <li>The pipeline efficiently extracted aggregated Device Profiles, App Profiles, and Suspicious Users from a large number of Bid Logs generated by Ad exchanges. Implemented batching to make predictions from the extracted data to increase the throughput.</li>
                <li>The data was read from GCS(Cloud storage) and written to GCS and BigQuery for further analysis. Used Protocol Buffers for data transfer between different steps.</li>
              </ul>
            </div>
          </div>

          <div className="project-card">
            <img
              src={project4}
            />
            <div class="text-block">
              <h3>Expense Tracker Web Application</h3>
              <h5>Technologies: Java, Spring Boot, Spring Security, MySQL, ReactJS, REST APIs, JWT, Reactstrap</h5>
            </div>
            <div className="hide">
              <ul>
                <li>Created an expense tracker web application using Java Spring Boot Framework for backend, React for frontend, and MySQL database to make the data persistent.</li>
                <li>Server-side Java application exposed API endpoints to run CRUD operations on the database and business logic on the server-side. React application consumed those endpoints to create a formatted view for the end-users.</li>
                <li>Used Spring Security to add authentication and authorization which returned a JWT token to be stored in the local storage of a browser for future use. The passwords were hashed using the bcrypt before being stored in the database.</li>
              </ul>
            </div>
          </div>

        </div>

      </div>
    );
  }
}

export default Projects;
